.resume-pdf-wrapper {
  /* padding: 2rem 1rem; */
  max-width: 1000px;
  margin: 0 auto;
  text-align: center;
  color: #e6e6e6;
}

.resume-pdf-title {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #ffffff;
  text-shadow: 0 0 8px #58a6ff33;
}

.resume-pdf-frame {
  width: 100%;
  height: 80vh;
  border: none;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(88, 166, 255, 0.1);
}
